export default {
	data() {
		return {
			yinyeShow: true,
			yinyeShow2: true,
			sfzzmImgUrl: "",
			sfzfmImgUrl: "",
			maskShow: false,
			
		}
	},
	methods: {
		uploadPic(v) {
			const that = this;
			if(v==='1'){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: [], //album 从相册选图，camera 使用相机，默认二者都有
					success: function (res) {
						const tempFilePaths = res.tempFilePaths;
						that.$http.uploadFiles({
							url: `${that.$baseUrl}/api/common/upload`,
							filePath: tempFilePaths[0],
							name: 'file',
							success: (res,status) => {
								uni.showToast({  
									title: '上传成功',
									icon:'none',  
									duration: 2000 
								});
								that.sfzzmImgUrl = res.data.url;
								that.yinyeShow = false;   
							}
						})
					},
					
				});
			}else if(v==='2'){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: [], //album 从相册选图，camera 使用相机，默认二者都有
					success: function (res) {
						const tempFilePaths = res.tempFilePaths;
						that.$http.uploadFiles({
							url: `${that.$baseUrl}/api/common/upload`,
							filePath: tempFilePaths[0],
							name: 'file',
							success: (res,status) => {
								uni.showToast({  
									title: '上传成功',
									icon:'none',  
									duration: 2000 
								});
								that.sfzfmImgUrl = res.data.url;
								that.yinyeShow2 = false;
							}
						}) 
					},
					
				});
			}  
			
		},
		formSubmit: function(e) {
		   const that = this;
		   const vs = e.detail.value;
		   const imgs = {
			   image1:this.sfzzmImgUrl,
			   image2:this.sfzfmImgUrl
		   };
		   const newO = Object.assign(vs,imgs);
		   that.$http.requestPost({
			   url:`${that.$baseUrl}/api/user/idCard`,
			   data: newO,
			   success: res=>{ 
				   uni.showToast({
						title: "提交成功", 
						icon:'none',
						duration: 2000
				   });
					uni.switchTab({
						url: "/pages/mine/mine"
				   })
			   }
		   })
		},
		/* togglePopup(open) {
			if (open === 'tip') {
				this.maskShow = true
			} 
		}, */
		/* cancel(v) {
			this.maskShow = false
			if (v === '1') {
				 uni.switchTab({
					url: "/pages/home/home"
				})
			} 
					 
		} */
	}
	
}
